@font-face {
  font-style: normal;
  font-weight: 700;
  font-family: "Helios-Cond";
  font-display: swap;
  src: url("../fonts/helios-cond.woff2") format("woff2"), url("../fonts/helios-cond.woff") format("woff");
}
@font-face {
  font-style: normal;
  font-weight: 400;
  font-family: "Roboto";
  font-display: swap;
  src: url("../fonts/roboto-regular.woff2") format("woff2"), url("../fonts/roboto-regular.woff") format("woff");
}
@font-face {
  font-style: normal;
  font-weight: 600;
  font-family: "Roboto";
  font-display: swap;
  src: url("../fonts/roboto-medium.woff2") format("woff2"), url("../fonts/roboto-medium.woff") format("woff");
}
@font-face {
  font-style: normal;
  font-weight: 700;
  font-family: "Roboto";
  font-display: swap;
  src: url("../fonts/roboto-bold.woff2") format("woff2"), url("../fonts/roboto-bold.woff") format("woff");
}
*,
*::before,
*::after {
  box-sizing: border-box;
}

html,
body {
  margin: 0;
  padding: 0;
  min-height: 100vh;
}

html {
  font-style: normal;
  font-weight: 400;
  font-size: 16px;
  line-height: 24px;
  font-family: "Placeholder", "Arial", sans-serif;
  color: #000000;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-rendering: optimizeLegibility;
}

body {
  width: 100%;
  height: 100%;
  background-color: #ffffff;
}

a {
  color: #000000;
  text-decoration: none;
}

img,
video {
  display: block;
  max-width: 100%;
  height: auto;
}

textarea {
  resize: none;
}

input:-webkit-autofill {
  box-shadow: inset 0 0 0 1000px #ffffff;
  -webkit-text-fill-color: #000000;
}

input,
textarea {
  border-radius: 0;
}
input::placeholder,
textarea::placeholder {
  opacity: 1;
}
input:invalid,
textarea:invalid {
  box-shadow: none;
}

select {
  border-radius: 0;
}

input::-ms-clear, input::-ms-reveal {
  display: none;
}

input[type=search]::-webkit-search-decoration,
input[type=search]::-webkit-search-cancel-button,
input[type=search]::-webkit-search-results-button,
input[type=search]::-webkit-search-results-decoration {
  appearance: none;
}

input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  margin: 0;
  appearance: none;
}

input[type=number] {
  appearance: textfield;
}

select,
textarea,
input:matches([type=email],
[type=number],
[type=password],
[type=search],
[type=tel],
[type=text],
[type=url]) {
  appearance: none;
}

button,
[type=button],
[type=reset],
[type=submit] {
  appearance: none;
}

.wrapper {
  position: relative;
  display: flex;
  flex-direction: column;
  min-height: 100vh;
  /* stylelint-disable-next-line */
  min-height: calc(100 * var(--vh, 1vh));
}
.wrapper .header,
.wrapper .footer {
  flex-shrink: 0;
}
.wrapper main {
  flex-grow: 1;
}

.rouble {
  font-family: "Rouble", "Arial", sans-serif;
  /* stylelint-disable */
  text-transform: lowercase !important;
  font-weight: 400 !important;
  /* stylelint-enable */
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  overflow: hidden;
  white-space: nowrap;
  border: 0;
  clip: rect(0 0 0 0);
  clip-path: inset(100%);
}

.scroll-lock-ios {
  position: fixed;
  overflow: hidden;
}

.scroll-lock {
  overflow: hidden;
}

.no-scrollbar {
  -ms-overflow-style: none;
  scrollbar-width: none;
}
.no-scrollbar::-webkit-scrollbar {
  display: none;
}

.no-transition {
  /* stylelint-disable-next-line */
  transition: none !important;
}

.container {
  width: 100%;
  max-width: 1440px;
  margin: 0 auto;
  padding: 0 135px;
}
@media (max-width: 1439px) {
  .container {
    padding: 0 40px;
  }
}
@media (max-width: 1023px) {
  .container {
    padding: 0 24px;
  }
}
@media (max-width: 767px) {
  .container {
    padding: 0 16px;
  }
}

.header {
  font-family: "Roboto", Arial, sans-serif;
}
.header .container {
  display: flex;
  flex-direction: column;
}
.header__top {
  display: flex;
  column-gap: 30px;
  padding: 16px 0 0;
  margin: 0 0 26px;
}
@media (max-width: 767px) {
  .header__top {
    flex-direction: column;
    align-items: center;
    row-gap: 20px;
  }
}
.header__bottom {
  border: 0;
  outline: 0;
}
.header__top-item {
  display: flex;
  flex-direction: column;
  row-gap: 21px;
  flex-grow: 1;
}
@media (max-width: 767px) {
  .header__top-item {
    width: 100%;
  }
}
.header__logo {
  min-width: 210px;
  min-height: 134px;
  color: #ffffff;
}
.header__logo img {
  width: 100%;
  max-height: 134px;
}
@media (max-width: 1279px) {
  .header__logo {
    min-width: 180px;
    min-height: 110px;
    height: 120px;
    width: 200px;
  }
}

.main-nav__list {
  display: flex;
  margin: 0;
  margin-right: 20px;
  margin-left: 60px;
  padding: 0;
  list-style: none;
}
.main-nav__item {
  margin-right: 20px;
}
.main-nav__link {
  color: #ffffff;
}
.main-nav__link.is-active {
  opacity: 0.6;
}
.main-nav__toggle {
  display: none;
}

.footer {
  padding: 40px 0;
}

.modal {
  position: fixed;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  z-index: 1000;
  display: flex;
  flex-direction: column;
  overflow-y: auto;
  -webkit-overflow-scrolling: touch;
  background-color: rgba(0, 0, 0, 0.8);
  transform: scale(1.2);
  visibility: hidden;
  opacity: 0;
  transition: opacity 0.6s cubic-bezier(0.55, 0, 0.1, 1), transform 0.6s cubic-bezier(0.55, 0, 0.1, 1), visibility 0.6s cubic-bezier(0.55, 0, 0.1, 1);
  pointer-events: none;
  -ms-overflow-style: none;
  scrollbar-width: none;
}
.modal::-webkit-scrollbar {
  display: none;
}
.modal__wrapper {
  position: relative;
  display: flex;
  flex-shrink: 0;
  justify-content: center;
  align-items: center;
  width: 100%;
  min-height: 100%;
  padding: 60px 40px;
}
@media (max-width: 767px) {
  .modal__wrapper {
    padding: 60px 16px;
  }
}
.modal__overlay {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  cursor: pointer;
}
.modal__content {
  position: relative;
  width: 600px;
  padding: 40px;
  background-color: #ffffff;
}
@media (max-width: 767px) {
  .modal__content {
    width: 100%;
    padding: 20px;
  }
}
.modal__close-btn {
  position: absolute;
  top: 22px;
  right: 22px;
  z-index: 1;
  width: 40px;
  height: 40px;
  padding: 0;
  background: rgba(255, 255, 255, 0);
  border: none;
  cursor: pointer;
}
.modal--responsive .modal__content {
  width: 100%;
}
.modal--fit-content .modal__content {
  width: auto;
}
@media (max-width: 767px) {
  .modal--fit-content .modal__content {
    width: 100%;
  }
}
.modal--no-scale {
  transform: none;
}
.modal--preload {
  transition: none;
}
.modal.is-active {
  transform: scale(1);
  visibility: visible;
  opacity: 1;
  pointer-events: auto;
}
.modal--menu .modal__wrapper {
  padding: 0;
  justify-content: flex-end;
}
.modal--menu .modal__content {
  width: 300px;
  height: 100%;
  padding: 50px 22px 40px;
}
@media (max-width: 767px) {
  .modal--menu .modal__content {
    width: 100%;
  }
}
.modal--menu .modal__menu-mob {
  display: flex;
  flex-direction: column;
  row-gap: 15px;
  padding: 0;
  margin: 0;
  list-style-type: none;
}
.modal--menu .modal__menu-mob-item {
  text-align: right;
}
.modal--menu .modal__close-btn {
  top: 12px;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 20px;
  height: 20px;
}

.btn {
  display: inline-flex;
  margin: 10px 0;
  padding: 10px 20px;
  color: #ffffff;
  border: none;
  cursor: pointer;
  transition: opacity 0.3s ease;
}
.btn:disabled {
  opacity: 0.5;
  pointer-events: none;
}
@media (hover: hover), screen and (min-width: 0\0 ) {
  .btn:hover, .btn:focus {
    opacity: 0.8;
  }
}

.custom-input,
.custom-textarea {
  position: relative;
  display: flex;
  width: 500px;
  padding: 20px 0;
  /*&.is-valid {
    input {
      border: 2px solid $color-malachite;

      &:focus,
      &:hover {
        border: 2px solid rgba($color-malachite, 0.5);
      }
    }
  }*/
}
.custom-input label,
.custom-textarea label {
  display: flex;
  align-items: center;
  width: 100%;
}
.custom-input label input,
.custom-input label textarea,
.custom-textarea label input,
.custom-textarea label textarea {
  flex-grow: 1;
  min-height: 40px;
  margin-left: 30px;
  padding: 10px;
  border: 2px solid rgba(0, 0, 0, 0.2);
  border-radius: 5px;
  outline: none;
  transition: border 0.3s ease;
}
.custom-input label input:hover,
.custom-input label textarea:hover,
.custom-textarea label input:hover,
.custom-textarea label textarea:hover {
  border: 2px solid rgba(0, 0, 0, 0.5);
}
.custom-input label input:focus,
.custom-input label textarea:focus,
.custom-textarea label input:focus,
.custom-textarea label textarea:focus {
  border: 2px solid rgba(0, 0, 0, 0.8);
}
.custom-input__error,
.custom-textarea__error {
  position: absolute;
  right: 0;
  bottom: -5px;
  color: red;
  opacity: 0;
  transition: opacity 0.3s ease;
}
.custom-input.is-invalid input,
.custom-textarea.is-invalid input {
  border: 2px solid red;
}
.custom-input.is-invalid input:focus, .custom-input.is-invalid input:hover,
.custom-textarea.is-invalid input:focus,
.custom-textarea.is-invalid input:hover {
  border: 2px solid red;
}
.custom-input.is-invalid .custom-input__error,
.custom-textarea.is-invalid .custom-input__error {
  opacity: 1;
}

.custom-toggle {
  position: relative;
  display: flex;
  width: 500px;
  padding: 20px 0;
  /*&.is-valid {
    label span {
      color: $color-eucalyptus;
    }
  }*/
}
.custom-toggle__error {
  position: absolute;
  bottom: 0;
  left: 0;
  color: red;
  opacity: 0;
  transition: opacity 0.3s ease;
}
.custom-toggle input {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  clip: rect(0 0 0 0);
}
.custom-toggle label {
  position: relative;
  padding-left: 26px;
  cursor: pointer;
}
.custom-toggle label span {
  margin-bottom: 0;
  margin-left: 0;
}
.custom-toggle--checkbox .custom-toggle__icon {
  position: absolute;
  top: 2px;
  left: 0;
  width: 16px;
  height: 16px;
  border: 1px solid rgba(0, 0, 0, 0.2);
  transition: border 0.3s ease;
}
.custom-toggle--checkbox .custom-toggle__icon::before {
  content: "";
  position: absolute;
  top: 50%;
  left: 50%;
  width: 6px;
  height: 6px;
  background-color: rgba(255, 255, 255, 0);
  transform: translate(-50%, -50%);
  transition: background-color 0.3s ease;
}
.custom-toggle--radio .custom-toggle__icon {
  position: absolute;
  top: 2px;
  left: 0;
  width: 16px;
  height: 16px;
  border: 1px solid rgba(0, 0, 0, 0.2);
  border-radius: 50%;
  transition: border 0.3s ease;
}
.custom-toggle--radio .custom-toggle__icon::before {
  content: "";
  position: absolute;
  top: 50%;
  left: 50%;
  width: 6px;
  height: 6px;
  color: rgba(255, 255, 255, 0);
  border-radius: 50%;
  transform: translate(-50%, -50%);
  transition: color 0.3s ease;
}
.custom-toggle input:checked + .custom-toggle__icon {
  border: 1px solid rgba(0, 0, 0, 0.8);
}
.custom-toggle input:checked + .custom-toggle__icon::before {
  background-color: rgba(0, 0, 0, 0.8);
}
.custom-toggle.is-invalid .custom-toggle__icon {
  border: 1px solid red;
}
.custom-toggle.is-invalid .custom-toggle__error {
  opacity: 1;
}

.custom-select {
  position: relative;
  width: 250px;
  padding: 30px 0;
  /* &.is-valid {
    .custom-select__button {
      border: 2px solid $color-eucalyptus;

      &:focus,
      &:hover {
        border: 2px solid rgba($color-eucalyptus, 0.5);
      }
    }
  } */
}
.custom-select__label {
  position: absolute;
  top: 50%;
  left: 10px;
  transform: translateY(-50%);
  transition: transform 0.3s ease, opacity 0.3s ease;
  pointer-events: none;
}
.custom-select__placeholder {
  position: absolute;
  top: 50%;
  left: 10px;
  transform: translateY(-50%);
  transition: transform 0.3s ease, opacity 0.3s ease;
  pointer-events: none;
}
.custom-select select {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  overflow: hidden;
  white-space: nowrap;
  border: 0;
  clip: rect(0 0 0 0);
  clip-path: inset(100%);
}
.custom-select__button {
  display: flex;
  align-items: center;
  width: 100%;
  height: 40px;
  padding: 5px 10px;
  background-color: rgba(255, 255, 255, 0);
  border: 2px solid rgba(0, 0, 0, 0.2);
  border-radius: 5px;
  outline: none;
  cursor: pointer;
  transition: border 0.3s ease;
}
.custom-select__button:hover {
  border: 2px solid rgba(0, 0, 0, 0.5);
}
.custom-select__button:focus {
  border: 2px solid rgba(0, 0, 0, 0.8);
}
.custom-select__list {
  position: absolute;
  bottom: 32px;
  z-index: 1;
  width: 100%;
  max-height: 100px;
  margin: 0;
  padding: 0;
  overflow-y: auto;
  list-style: none;
  background-color: #ffffff;
  border: 2px solid #000000;
  transform: translateY(100%);
  visibility: hidden;
  opacity: 0;
  transition: visibility 0.3s ease, opacity 0.3s ease;
}
.custom-select__item {
  padding: 5px;
  outline: none;
  cursor: pointer;
  transition: background-color 0.3s ease;
}
.custom-select__item[aria-selected=true] {
  background-color: rgba(255, 255, 255, 0.2);
}
@media (hover: hover), screen and (min-width: 0\0 ) {
  .custom-select__item[aria-selected=true]:hover, .custom-select__item[aria-selected=true]:focus {
    background-color: rgba(255, 255, 255, 0.2);
  }
}
@media (hover: hover), screen and (min-width: 0\0 ) {
  .custom-select__item:hover, .custom-select__item:focus {
    background-color: rgba(255, 255, 255, 0.4);
  }
}
.custom-select__error {
  position: absolute;
  bottom: 0;
  left: 0;
  color: red;
  opacity: 0;
  transition: opacity 0.1s ease;
}
.custom-select__text {
  flex-shrink: 0;
  width: 80%;
  overflow: hidden;
  text-align: left;
  white-space: nowrap;
  text-overflow: ellipsis;
  opacity: 0;
  transition: opacity 0.3s ease 0.1s;
}
.custom-select__text br {
  display: none;
}
.custom-select__icon {
  flex-shrink: 0;
  width: 20px;
  height: 10px;
  margin-left: auto;
  background-color: #000000;
  transition: transform 0.3s ease;
  clip-path: polygon(100% 0, 100% 20%, 50% 100%, 0 20%, 0 0, 50% 80%);
}
.custom-select.open-up .custom-select__list {
  bottom: auto;
  top: 0;
  transform: translateY(calc(-100% + 30px));
}
.custom-select.is-open .custom-select__list {
  visibility: visible;
  opacity: 1;
}
.custom-select.is-open .custom-select__icon {
  transform: rotate(180deg);
}
.custom-select.is-open .custom-select__label {
  opacity: 0;
}
.custom-select.not-empty .custom-select__text {
  opacity: 1;
}
.custom-select.not-empty .custom-select__label {
  opacity: 0;
}
.custom-select.not-empty .custom-select__placeholder {
  opacity: 0;
}
.custom-select.is-invalid .custom-select__error {
  opacity: 1;
}
.custom-select.is-invalid .custom-select__button {
  border: 2px solid red;
}
.custom-select.is-invalid .custom-select__button:focus, .custom-select.is-invalid .custom-select__button:hover {
  border: 2px solid red;
}
.custom-select--placeholder .custom-select__label {
  transform: translateY(calc(-50% - 40px));
  opacity: 1;
}
.custom-select--placeholder.is-open .custom-select__label {
  opacity: 1;
}
.custom-select--placeholder.not-empty .custom-select__label {
  opacity: 1;
}
.custom-select.slide-up.not-empty .custom-select__label, .custom-select.slide-up.is-open .custom-select__label {
  transform: translateY(calc(-50% - 40px));
  opacity: 1;
}
.custom-select.slide-up.not-empty .custom-select__text, .custom-select.slide-up.is-open .custom-select__text {
  opacity: 1;
}

.nav-bar {
  display: flex;
  justify-content: space-between;
  align-items: center;
  position: relative;
  font-weight: 400;
  font-size: 12px;
  line-height: 14px;
  letter-spacing: -0.01em;
  color: #252525;
  padding: 0 0 9px;
}
@media (max-width: 1023px) {
  .nav-bar {
    flex-direction: row-reverse;
  }
}
.nav-bar__reg {
  display: flex;
  column-gap: 28px;
}
.nav-bar__wrapper-mob {
  border: 0;
  outline: 0;
  padding: 0;
  margin: 0;
  background-color: rgba(255, 255, 255, 0);
  color: #252525;
}
.nav-bar__reg-enter, .nav-bar__reg-reg {
  padding: 0;
  margin: 0;
  border: 0;
  outline: none;
  line-height: 14px;
  background-color: rgba(255, 255, 255, 0);
  cursor: pointer;
  transition: all 0.3s ease;
}
.nav-bar__reg-enter:hover, .nav-bar__reg-reg:hover {
  text-decoration: underline;
}
.nav-bar__reg-enter {
  position: relative;
}
.nav-bar__reg-enter:after {
  content: "";
  position: absolute;
  top: 0;
  right: -13.5px;
  width: 1px;
  height: 100%;
  background-color: #000000;
}
.nav-bar::after {
  content: "";
  position: absolute;
  bottom: 0;
  left: 0;
  width: 100%;
  height: 1px;
  background-color: #D7D7D7;
}
.nav-bar__wrapper {
  height: 100%;
}
@media (max-width: 1023px) {
  .nav-bar__wrapper {
    display: none;
  }
}
.nav-bar__list {
  display: flex;
  align-items: center;
  height: 100%;
  margin: 0;
  padding: 0;
  list-style-type: none;
  column-gap: 22px;
}
@media (max-width: 1439px) {
  .nav-bar__list {
    column-gap: 11px;
  }
}
.nav-bar__link {
  white-space: nowrap;
  transition: all 0.3s ease;
}
.nav-bar__link:hover {
  color: #000000;
  text-decoration: underline;
}

.wrapper-info {
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
  font-family: "Roboto", Arial, sans-serif;
  font-weight: 400;
  font-size: 14px;
  letter-spacing: -0.01em;
}
@media (max-width: 767px) {
  .wrapper-info {
    flex-direction: column;
    align-items: center;
    row-gap: 20px;
  }
}
.wrapper-info__wrap {
  display: flex;
  column-gap: 30px;
}
@media (max-width: 1023px) {
  .wrapper-info__wrap {
    column-gap: 10px;
    row-gap: 20px;
  }
}
@media (max-width: 767px) {
  .wrapper-info__wrap {
    width: 100%;
    flex-direction: column;
    align-items: center;
  }
}
.wrapper-info__links {
  height: 100%;
}
.wrapper-info__adv {
  width: 253px;
  height: 70px;
  background-color: #E2E2E2;
}
@media (max-width: 1023px) {
  .wrapper-info__adv {
    width: 120px;
  }
}
@media (max-width: 767px) {
  .wrapper-info__adv {
    width: 100%;
  }
}
.wrapper-info__items {
  display: flex;
  column-gap: 15px;
}
.wrapper-info__items a {
  transition: all 0.3s ease;
}
.wrapper-info__items a img {
  width: 100%;
  height: 100%;
}
.wrapper-info__items a:hover {
  box-shadow: 0px 0px 7px 0px rgba(0, 0, 0, 0.4);
}
@media (max-width: 1279px) {
  .wrapper-info__items {
    display: none;
  }
}

.icons-links {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  row-gap: 11px;
  height: 100%;
  list-style-type: none;
  margin: 0;
  padding: 0;
}
.icons-links__link {
  display: flex;
  align-items: flex-end;
  column-gap: 11px;
  line-height: 14px;
  transition: all 0.3s ease;
}
.icons-links__link:hover .icons-links__wrapper-text {
  text-decoration: none;
}
.icons-links__link svg {
  width: 19px;
}
.icons-links__link:first {
  line-height: 16px;
}
.icons-links__wrapper-text {
  display: flex;
  text-decoration: underline;
}
.icons-links__text {
  white-space: pre;
}

.menu {
  display: flex;
  justify-content: space-between;
  align-items: center;
  box-shadow: 0px 2px 3px rgba(0, 0, 0, 0.25);
  border-radius: 10px 10px 10px 10px;
  background-color: rgba(255, 255, 255, 0);
}
@media (max-width: 767px) {
  .menu {
    flex-direction: column;
  }
}
.menu__search {
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 8px 10px 7px;
  background-color: #FFCC00;
  border-radius: 10px 0 0 10px;
  border: 0;
  outline: 0;
  cursor: pointer;
  transition: all 0.3s ease;
}
.menu__search:hover {
  color: #ffffff;
}
@media (max-width: 767px) {
  .menu__search {
    width: 100%;
    border-radius: 10px 10px 0 0;
  }
}
.menu__link {
  display: inline-block;
  padding: 9px 19px;
  font-weight: 600;
  font-size: 14px;
  line-height: 16px;
  color: #ffffff;
  transition: all 0.3s ease;
  white-space: nowrap;
}
.menu__link:hover {
  color: #FFCC00;
}
.menu__link--active {
  background-color: #FFCC00;
  color: #000000;
}
.menu__link--active:hover {
  color: #000000;
}
@media (max-width: 767px) {
  .menu__link {
    width: 100%;
    text-align: center;
  }
}
.menu__sale-link {
  padding: 10px 15px 8px 14px;
  height: 100%;
  border: 0;
  outline: 0;
  background-color: #FFCC00;
  text-transform: uppercase;
  border-radius: 0 10px 10px 0;
  font-weight: 600;
  font-size: 14px;
  line-height: 16px;
  transition: all 0.3s ease;
  cursor: pointer;
}
.menu__sale-link:hover {
  color: #ffffff;
}
@media (max-width: 767px) {
  .menu__sale-link {
    width: 100%;
    border-radius: 0 0 10px 10px;
  }
}
.menu__list {
  display: flex;
  height: 100%;
  min-height: 100%;
  flex-grow: 1;
  list-style-type: none;
  margin: 0;
  padding: 0 20px 0 59px;
  background-color: #252525;
}
@media (max-width: 1439px) {
  .menu__list {
    padding: 0 30px;
    overflow: hidden;
  }
}
@media (max-width: 1023px) {
  .menu__list {
    padding: 0 10px;
    overflow: auto;
  }
}
@media (max-width: 767px) {
  .menu__list {
    flex-direction: column;
    width: 100%;
    padding: 0;
  }
}
@media (max-width: 767px) {
  .menu__menu-item {
    width: 100%;
  }
}

.reg {
  display: flex;
  flex-direction: column;
}
.reg__time {
  display: flex;
  justify-content: space-between;
  column-gap: 27px;
  margin: 0 0 15px;
}
@media (max-width: 1023px) {
  .reg__time {
    column-gap: 15px;
    margin: 0 0 8px;
  }
}
.reg__time span:first-child {
  font-weight: 700;
  font-size: 12px;
  line-height: 14px;
  letter-spacing: -0.01em;
}
.reg__time span:last-child {
  font-weight: 400;
  font-size: 12px;
  line-height: 14px;
  letter-spacing: -0.025em;
}
.reg__tel {
  margin: 0 0 16px;
  position: relative;
}
.reg__tel:after {
  content: "";
  position: absolute;
  bottom: -12px;
  width: 100%;
  height: 1px;
  left: 0;
  background-color: #D7D7D7;
}
@media (max-width: 1023px) {
  .reg__tel:after {
    bottom: -6px;
  }
}
@media (max-width: 1023px) {
  .reg__tel {
    text-align: right;
    margin: 0 0 10px;
  }
}
.reg__tel a {
  font-weight: 700;
  font-size: 25px;
  line-height: 29px;
  height: 29px;
  letter-spacing: -0.02em;
  transition: all 0.3s ease;
}
.reg__tel a:hover {
  text-decoration: underline;
}
@media (max-width: 1023px) {
  .reg__tel a {
    font-size: 20px;
    line-height: 23px;
    height: 23px;
  }
}
.reg__place {
  display: flex;
  justify-content: space-between;
  line-height: 14px;
}
.reg__change-place {
  position: relative;
  margin: 0;
  padding: 0 0 0 11px;
  border: 0;
  outline: 0;
  background-color: rgba(255, 255, 255, 0);
  text-decoration: underline dotted;
  cursor: pointer;
  line-height: 14px;
}
.reg__change-place:before {
  content: "";
  height: 7px;
  width: 7px;
  position: absolute;
  top: 50%;
  left: 0;
  transform: translate(0, -50%);
  background-image: url("../../img/svg/icon-arrow.svg");
  transition: all 0.3s ease;
}
.reg__change-place:hover:before {
  transform-origin: 50% 50%;
  transform: translate(0, -50%) rotate(90deg);
}

